/* TOPページのボタン */
.index-btn {
  text-align: center;
  margin: $space_20-30px auto;
  & a {
    position: relative;
    font-size: $font_19-22px;
    color: $gray;
    display: inline-block;
    padding: calc($space_16-18px * 1.5) $space_20-30px;
    border: 1px solid $gray;
    transition: all 0.4s ease-in-out;
    &::before,
    &::after {
      content: "";
      position: absolute;
      width: 10px;
      height: 10px;
      border: solid $gray;
      transition: all 0.4s ease-in-out;
    }
    &::before {
      top: -6px;
      left: -6px;
      border-width: 1px 0 0 1px;
    }
    &::after {
      bottom: -6px;
      right: -6px;
      border-width: 0 1px 1px 0;
    }
    &:hover {
      color: $white;
      background-color: $red3;
      &:hover::before,
      &:hover::after {
        width: calc(100% + 11px);
        height: calc(100% + 11px);
        border-color: $red3;
      }
    }
  }
}

/* スクロール時のフェード機能 【JSで制御】 =============================== */
// 下から
.scroll-fadeUp {
  display: block;
  opacity: 0;
  transform: translate(0, 80px);
  transition: all 1500ms;
}
.scroll-fadeUp.scrollIn {
  opacity: 1;
  transform: translate(0, 0);
}

// 上から
.scroll-fadeDown {
  display: block;
  opacity: 0;
  transform: translate(0, -80px);
  transition: all 1500ms;
}
.scroll-fadeDown.scrollIn {
  opacity: 1;
  transform: translate(0, 0);
}

// 左から
.scroll-fadeLeft {
  display: block;
  opacity: 0;
  transform: translate(-80px, 0);
  transition: all 1500ms;
}
.scroll-fadeLeft.scrollIn {
  opacity: 1;
  transform: translate(0, 0);
}

// 右から
.scroll-fadeRight {
  display: block;
  opacity: 0;
  transform: translate(80px, 0);
  transition: all 1500ms;
}
.scroll-fadeRight.scrollIn {
  opacity: 1;
  transform: translate(0, 0);
}

/* HEADER ============================================== */

header {
  background-color: $black;
  padding: $space_10-15px;
  & * {
    display: flex;
    align-items: center;
  }
  & .header-contents {
    justify-content: space-between;
    & .header-title {
      margin: 0 $space_16-18px;
      color: $white;
      font: {
        size: $font_19-22px;
        weight: 900;
      }
    }
    & .header-nav-contents {
      & .header-nav {
        & li {
          & a {
            display: block;
            font-size: $font_11-13px;
            padding: $space_10-15px;
            color: $white;
            transition: 400ms;
            & span {
              display: block;
              text-align: center;
              margin-top: 0.25rem;
              color: $gray;
            }
            &:hover {
              background-color: $red3;
              color: $white;
              transition: 400ms;
            }
          }
        }
      }
      & .header-link {
        margin: 0 $space_16-18px;
        & li {
          & a {
            display: block;
            border-radius: 10%;
            width: 3rem;
            height: 3rem;
            margin: 0.5rem;
            color: $white;
            position: relative;
            transition: 400ms;
            & .fab {
              position: absolute;
              top: 0.5rem;
              left: 0.6rem;
              font-size: 2rem;
            }
            &:hover {
              background-color: $red3;
              color: $white;
              transition: 400ms;
            }
          }
        }
      }
    }
  }
}

// HEADER CLONE 【JSで制御】
.header-clone {
  background-color: $black;
  padding: 0.5rem;
  position: fixed;
  top: -130px;
  width: 100%;
  z-index: 30;
  transition: top 0.25s;
  opacity: 0.9;
  & * {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  & > .header-contents {
    & .header-title {
      margin: 0 $space_16-18px;
      color: $white;
      font: {
        size: $font_19-22px;
        weight: 900;
      }
    }
    & .header-nav-contents {
      & .header-nav {
        & li {
          & a {
            display: block;
            font-size: $font_11-13px;
            padding: $space_10-15px;
            color: $white;
            transition: 400ms;
            & span {
              display: none;
            }
            &:hover {
              background-color: $red3;
              color: $white;
              transition: 400ms;
            }
          }
        }
      }
      & .header-link {
        margin: 0 $space_16-18px;
        & li {
          & a {
            border-radius: 10%;
            width: 3rem;
            height: 3rem;
            margin: 0.5rem;
            transition: 400ms;
            position: relative;
            & .fab {
              color: $white;
              position: absolute;
              top: 0.6rem;
              left: 0.8rem;
              font-size: 1.5rem;
            }
            &:hover {
              background-color: $red3;
              color: $white;
              transition: 400ms;
            }
          }
        }
      }
    }
  }
  // cloneの表示
  &.visible {
    top: 0;
  }
}

