// トップスライダー
.index-top-slider {
  & li {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: none;
    opacity: 0;
    animation: anime 50s infinite;
  }
  & li:nth-child(1) {
    background-image: url("../../dist/images/img12.jpeg");
    animation-delay: 0s;
  }
  & li:nth-child(2) {
    background-image: url("../../dist/images/img13.jpeg");
    animation-delay: 10s;
  }
  & li:nth-child(3) {
    background-image: url("../../dist/images/img14.jpeg");
    animation-delay: 20s;
  }
  & li:nth-child(4) {
    background-image: url("../../dist/images/img15.jpeg");
    animation-delay: 30s;
  }
}

@keyframes anime {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  40% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

// ページロード時のアニメーション(.index-top-textに指定) 【JSで制御】
// 上からのアニメーション
.load-down {
  opacity: 0;
  transform: translate(0, -50px);
  transition-timing-function: ease-in-out;
}

.load-downed {
  transform: translate(0, 0);
  opacity: 1;
  transition: all 800ms;
  transition-delay: 200ms;
  transition-timing-function: ease-in-out;
}

// 下からのアニメーション
.load-up {
  opacity: 0;
  transform: translate(0, 50px);
  transition-timing-function: ease-in-out;
}

.load-uped {
  transform: translate(0, 0px);
  transform: translate(0 0);
  opacity: 1;
  transition: all 800ms;
  transition-delay: 200ms;
  transition-timing-function: ease-in-out;
}

// トップスライダーのテキスト
.index-top-text {
  background: rgba(255, 255, 255, 0);
  display: inline-block;
  width: 30vw;
  height: 70vh;
  text-align: center;
  padding: $space_16-18px $space_40-60px $space_40-60px;
  position: absolute;
  top: $space_40-60px;
  right: $space_40-60px;
  & h1 {
    font: {
      size: calc($font_32-64px * 0.8);
    }
  }
  & .sub-title {
    font: {
      size: $font_18-20px;
      weight: 900;
    }
    margin-bottom: $space_16-18px;
  }
}

// ページロード後に背景色が変わるアニメーション 【JSで制御】
.index-top-text-bg {
  animation-name: bgChange;
  animation-duration: 1s;
  animation-timing-function: ease;
  animation-fill-mode: forwards;
}
@keyframes bgChange {
  0% {
    background-color: rgba(255, 255, 255, 0);
  }
  100% {
    background-color: rgba(255, 255, 255, 0.9);
  }
}

// スクロールダウンのボタン 【JSで制御】
.index-top-btn {
  display: inline-block;
  text-align: center;
  margin-top: calc($space_30-45px * 0.8);
  padding: $space_20-30px $space_80_120px;
  border: 2px solid $gray;
  border-radius: 10px;
  z-index: 2;
  animation: bounce 2s infinite 2s;
  transition: all 0.2s ease-in;
  transform: scale(1);

  // 矢印のレイアウト
  &::before {
    position: absolute;
    top: calc(50% - 18px);
    left: calc(50% - 14px);
    transform: rotate(-45deg);
    display: block;
    width: 20px;
    height: 20px;
    content: "";
    border: 2px solid $gray;
    border-width: 0 0 2px 2px;
  }
}

@keyframes bounce {
  0%,
  100%,
  20%,
  50%,
  80% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

// TOPページ(index)の各コンテンツに指定
.section {
  position: relative;
  padding: $space_60_90px 0 $space_30-45px;
}

// 各sectionの背景画像
.section-bg {
  margin: calc($space_40-60px * 1.5) 0;
  & img {
    width: 100%;
    height: 450px;
    object-fit: cover;
  }
}

// TOPページの「section title」のレイアウト
.section-title {
  text-align: center;
  display: inline-block;
  padding: calc($space_20-30px * 2.5) $space_40-60px;
  color: $white;
  & h2 {
    font-size: $font_24-27px;
    margin: $space_16-18px 0 0.7rem;
  }
  & span {
    font-size: $font_16-18px;
  }
}

.index-profile-title {
  position: absolute;
  top: -14rem;
  left: 6rem;
  background-color: $green;
}

.index-gallery-title {
  position: absolute;
  top: -12rem;
  right: 8rem;
  background-color: $red3;
}

.index-job-title {
  position: absolute;
  top: -12rem;
  left: 8rem;
  background-color: $blue;
}

/* TOP NEWS  =========================================== */
.index-news-content {
  margin: $space_40-60px;
  & h2 {
    font-size: calc($font_24-27px * 1.5);
    margin-bottom: $space_20-30px;
  }
  & .index-news-articles {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: $space_20-30px;
    margin-bottom: $space_60_90px;
    & .index-news-item {
      display: flex;
      justify-content: space-around;
      align-items: center;
      padding: $space_16-18px;
      border: 1px solid $lightGray;
      box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.4);
      transition: background-color 0.5s;
      & img {
        width: 50%;
        transition: transform 0.5s;
      }
      & .index-news-text {
        margin-left: $space_16-18px;
        & h3 {
          font-size: $font_18-20px;
          margin-bottom: $space_10-15px;
        }
        & p {
          font-size: calc($font_11-13px * 1.2);
          margin-bottom: calc($space_10-15px * 0.5);
        }
      }
      &:hover {
        background-color: $smoke;
        & img {
          transform: scale(1.1);
        }
      }
    }
  }
}

/* TOP Profile, Profile ================================ */
.profile-content {
  margin-bottom: $space_60_90px;
  padding: $space_40-60px $space_20-30px;
  background: linear-gradient(to right, #fffcdc, #d9a7c7);
  & > * {
    display: flex;
    justify-content: space-around;
  }
  & img {
    object-fit: cover;
    width: 35vw;
  }
  & p {
    margin-bottom: $space_16-18px;
    font-size: $font_16-18px;
  }
}

.profile {
  flex-direction: row-reverse;
  margin-top: $space_20-30px;
  & .profile-text {
    padding: $space_60_90px $space_30-45px $space_30-45px;
    width: 35vw;
    background-color: rgba(255, 255, 255, 0.4);
    & span {
      font-size: $font_19-22px;
    }
    & h2 {
      font-size: calc($font_32-64px * 0.6);
      margin: calc($space_10-15px * 0.5) 0 $space_30-45px;
    }
  }
}

.message {
  margin-top: $space_60_90px;
  .message-text {
    background-color: rgba(255, 255, 255, 0.4);
    padding: $space_30-45px;
    width: 35vw;
    & .message-title {
      font-size: $font_24-27px;
      margin-bottom: $space_16-18px;
    }
  }
}

.achievements {
  position: relative;
  margin: $space_60_90px auto;
  & .achievements-title {
    background-color: $blue;
    position: absolute;
    top: -18rem;
  }
  .achievements-content {
    margin-left: $space_40-60px;
    padding: $space_20-30px 0 $space_30-45px;
    & h3 {
      font-size: $font_19-22px;
      margin-bottom: $space_16-18px;
    }
    & li {
      line-height: 2;
    }
  }
}

/* TOP GALLERY ========================================= */

.index-gallery-content {
  display: grid;
  width: 80vw;
  gap: 2.5vw;
  grid-template-columns: repeat(3, 26vw);
  grid-template-rows: repeat(3, 26vw);
  margin: $space_80_120px auto;
  & .index-gallery-item {
    margin: auto;
    position: relative;
    width: 100%;
    height: 100%;
    transition: 0.4s;
    transition-timing-function: ease-in-out;
    & img {
      display: block;
      width: 100%;
      height: 100%;
    }
    & .index-gallery-text {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: opacity 0.3s;
      opacity: 0;
      & * {
        display: block;
        text-align: center;
        font-size: $font_18-20px;
        margin: $space_10-15px;
        opacity: 0;
      }
    }
    &:hover {
      cursor: pointer;
      transform: scale(1.1, 1.1);
      z-index: 10;
      & .index-gallery-text {
        background-color: $white;
        border-radius: 50%;
        transition: 0.7s;
        opacity: 0.8;
        & .index-gallery-text1 {
          animation: style-top 0.5s;
          animation-delay: 0.5s;
          animation-fill-mode: forwards;
          animation-timing-function: ease-in-out;
        }
        & .index-gallery-text2 {
          animation: style-top 0.5s;
          animation-delay: 0.5s;
          animation-fill-mode: forwards;
          animation-timing-function: ease-in-out;
        }
        & .index-gallery-text3 {
          animation: style-bottom 0.5s;
          animation-delay: 0.5s;
          animation-fill-mode: forwards;
          animation-timing-function: ease-in-out;
        }
      }
    }
  }
}

// 上から表示するアニメーション
@keyframes style-top {
  0% {
    transform: translateY(-50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}

// 下から表示するアニメーション
@keyframes style-bottom {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}

/* TOP PRICE =========================================== */
.index-job-content {
  width: 70%;
  display: flex;
  margin: $space_60_90px auto;
  & .index-job-item {
    display: flex;
    flex-flow: column;
    overflow: hidden;
    line-height: 0;
    & img {
      width: 100%;
      height: 305px;
    }
    & .index-job-text {
      background-color: $black;
      color: $white;
      padding: $space_30-45px $space_10-15px;
      text-align: left;
      height: 220px;
      & .text1 {
        text-align: center;
        font-size: $font_18-20px;
      }
      & .text2 {
        margin: $space_16-18px $space_10-15px 0;
        font-size: calc($font_16-18px * 0.9);
        line-height: 1.8;
      }
    }
    & .number1 {
      order: 1;
    }
    & .number2 {
      order: 2;
    }
    & .number3 {
      order: 4;
    }
    & .number4 {
      order: 3;
    }
    & .number5 {
      order: 5;
    }
    & .number6 {
      order: 6;
    }
  }
}

.index-price-content {
  display: flex;
  justify-content: space-between;
  width: 90%;
  margin: 0 auto;
  padding: $space_40-60px 0;
  & .index-price-item {
    text-align: center;
    width: 30%;
    background-color: $white;
    border: 1px solid $lightGray;
    padding: $space_30-45px $space_16-18px;
    margin: 0 $space_16-18px;
    & img {
      height: 30vh;
      width: 23vw;
    }
    & h3 {
      font-size: $font_19-22px;
      margin: $space_10-15px auto calc($space_16-18px * 1.2);
    }
    & p {
      font-size: $font_16-18px;
    }
  }
}

/* GALLERY ================================= */
.gallery-content {
  margin: $space_60_90px auto;
  & p {
    text-align: center;
    font-size: $font_18-20px;
    margin-bottom: $space_30-45px;
    line-height: 2;
  }
  & .gallery {
    width: 80vw;
    display: grid;
    gap: 2vw;
    grid-template-columns: repeat(3, 25vw);
    grid-template-rows: repeat(6, 25vw);
    margin: 0 auto;
  }
  & .gallery-item {
    transition: 0.4s;
    transition-timing-function: ease-in-out;
    & img {
      width: 100%;
      height: auto;
    }
    & .caption {
      display: none;
    }
    & .is-loading {
      opacity: 0;
    }
    &:hover {
      transform: scale(1.1, 1.1);
      z-index: 10;
    }
  }
}

#cboxTitle {
  font-size: $font_16-18px;
  padding: $space_16-18px;
  background-color: $red3;
}

/* NEWS ==================================== */
.news-item {
  background-color: $white;
  width: 80vw;
  display: flex;
  justify-content: space-between;
  padding: $space_20-30px;
  margin: $space_40-60px auto;
  border: 1px solid $lightGray;
  box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.4);
  transition: background-color 0.5s;
  & img {
    transition: transform 0.5s;
  }
  & .news-text {
    width: 100%;
    margin-left: $space_20-30px;
    & h2 {
      font-size: $font_19-22px;
      margin-bottom: $space_16-18px;
    }
    & .news-time {
      font-size: $font_16-18px;
      color: $gray;
      margin-bottom: $space_16-18px;
    }
    & .news-desc {
      font-size: $font_16-18px;
    }
  }
  &:hover {
    background-color: $smoke;
    & img {
      transform: scale(1.1);
    }
  }
}

