/* PRICE =================================== */
.price-content {
  max-width: 1000px;
  padding: $space_60_90px;
  background-color: $white;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.2);
  margin: $space_80_120px auto;
  .price-title {
    margin-bottom: $space_40-60px;
    & h3 {
      text-align: center;
      font-size: $font_19-22px;
      margin-bottom: $space_16-18px;
    }
  }
  & .price-design-title {
    font-size: $font_18-20px;
    font-weight: bold;
    margin-bottom: $space_16-18px;
  }
  & .price-design {
    border: 1px solid $lightGray;
    background-color: $whiteSmoke;
    box-sizing: border-box;
    display: flex;
    padding: $space_20-30px;
    margin-bottom: $space_40-60px;
    & img {
      width: 35vw;
      height: auto;
    }
    & .price-design-box {
      display: flex;
      flex-direction: column;
      width: 100%;
      margin-left: $space_40-60px;
      & ul {
        width: 70%;
        & li {
          font-size: $font_18-20px;
          background-color: $gray;
          color: $white;
          margin: calc($space_16-18px * 0.5) 0;
          padding: calc($space_16-18px * 0.8) $space_16-18px;
          border-radius: 30px;
        }
      }
      & .price-design-btn {
        display: inline-block;
        font-size: 1.2rem;
        padding: 1rem 2rem;
        width: auto;
        color: $white;
        background-color: $black;
        margin: 2rem 0 0 2rem;
        text: {
          align: center;
          decoration: none;
        }
      }
    }
  }
}

.price-hosoku {
  margin: 0 auto;
  padding-left: -1rem;
  text-indent: -1rem;
}
.price-table {
  width: 100%;
  margin-bottom: 1rem;
  border-collapse: collapse;
  border: 1px solid $lightGray;
  & th {
    background-color: $whiteSmoke;
  }
  & th,
  td {
    width: 35vw;
    padding: 1rem 2rem;
    text-align: center;
    border: 1px solid $lightGray;
  }
  & td:first-child {
    text-align: left;
    background-color: $whiteSmoke;
  }
}
.price-table2 {
  @extend .price-table;
  & td:first-child {
    width: 50%;
  }
}

/* Timeline */
.timeline-item {
  display: flex;
}
.timeline-title {
  font-size: 1.5rem;
  width: 24%;
  padding: 3.5rem 4rem 2rem 0;
  text-align: right;
}
.timeline-content {
  margin: 0;
  width: 76%;
  //padding: 1.5rem 0 1.5rem 4rem;
  position: relative;
  border-left: 5px solid $lightGray;
}
.timeline1 {
  padding: 2.5rem 0 2.5rem 4rem;
}
.timeline2 {
  padding: 1.5rem 0 1.5rem 4rem;
}
.timeline3 {
  padding: 2rem 0 3.5rem 4rem;
}
.timeline4 {
  padding: 2.5rem 0 1.5rem 4rem;
}
.timeline5 {
  padding: 3.5rem 0 3.5rem 4rem;
}
.timeline-content::before {
  display: block;
  position: absolute;
  top: 2.5rem;
  left: -2.5rem;
  width: 3.5rem;
  height: 3.5rem;
  content: "";
  background-color: $whiteSmoke;
  text-align: center;
  /* Font Awesome */
  font: {
    family: "Font Awesome 5 Free";
    size: 1.7rem;
    weight: 900;
  }
  line-height: 1.5;
  color: $black;
  padding: 0.8rem 0.5rem 0 0.5rem;
  border-radius: 10px;
}
.icon-file::before {
  content: "\f044";
}
.icon-meeting::before {
  content: "\f086";
}
.icon-payment::before {
  content: "\f157";
}
.icon-production::before {
  content: "\f1fc";
}
.icon-complete::before {
  content: "\f4df";
}
.price-btn {
  background-color: $red2;
  color: $white;
  margin-top: 3rem;
}

/* CONTACT ================================= */
.contact-text p {
  margin: 0.5rem 0;
}
.small-text {
  font-size: 0.8rem;
  margin-bottom: 2rem;
}

/* Table */
.contact-table {
  border-spacing: 0;
  width: 100%;
  border: 1px solid $lightGray;
  margin-bottom: 2rem;
  & th,
  td {
    display: table-cell;
    padding: 1.3rem;
  }
  & th {
    font-weight: normal;
    width: 24%;
    vertical-align: middle;
  }
  & td {
    width: 76%;
  }
  & tr:nth-child(odd) {
    background: $smoke;
  }
  & tr:nth-child(even) {
    background: $white;
  }
}
.must {
  color: $red;
}

/* FORM */
input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  width: 100%;
  border: 1px solid $lightGray;
  background-color: $white;
  padding: 0.5rem 0 0.5rem 0.5rem;
}
input[type="text"]::placeholder,
input[type="email"]::placeholder,
input[type="tel"]::placeholder,
textarea::placeholder {
  color: $lightGray;
}
textarea {
  height: 10rem;
}
#postcode1,
#postcode2 {
  width: 30%;
}
.select-box {
  width: 50%;
  text-align: center;
  padding: 0.5rem;
}
.btn-send {
  color: $white;
  background-color: $red2;
}

/* チェックボックスの非表示 */
input[type="checkbox"] {
  display: none;

  /* チェックボックスを変更 */
  & + span::before {
    display: inline-block;
    content: "";
    width: 2rem;
    height: 2rem;
    border: 1px solid $lightGray;
    background-color: $white;
    vertical-align: middle;
    margin: -0.25rem 0.5rem 0 1.85rem;
    padding: 0 0 0.25rem 0.5rem;
    /* Font Awesome */
    font: {
      family: "Font Awesome 5 Free";
      size: 1.5rem;
      weight: 900;
    }
    line-height: 1.5;
  }
}
input[type="checkbox"]:checked + span::before {
  content: "\f00c";
}

/* POLICY =========================================== */
.policy-text {
  & p {
    margin: 0.5rem 0 3rem;
  }
  & h3 {
    font: {
      size: 1.25rem;
      weight: 900;
    }
    margin: 0;
  }
}

/* FOOTER =========================================== */
footer {
  text-align: center;
  background-color: $black;
  color: $white;
}
.footer-policy {
  margin: 0 auto;
  background-color: $black;
  & a {
    text-align: center;
    display: block;
    padding: 2rem;
    color: $white;
    text-decoration: none;
  }
}

/* .footer-link */
.footer-link {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  padding: 2rem;
  & li {
    list-style-type: none;
    & a {
      display: block;
      border-radius: 10%;
      width: 3rem;
      height: 3rem;
      margin: 0;
      padding: 1rem;
      color: $white;
      position: relative;
      & .fab {
        position: absolute;
        top: 1.5rem;
        left: 1.5rem;
        font-size: 2rem;
      }
    }
  }
}

/* ページトップに戻るボタン */
.back-to-top {
  position: fixed;
  right: 10px;
  bottom: 10px;
  & a {
    position: relative;
    display: block;
    width: 80px;
    height: 80px;
    text-decoration: none;
    &::before {
      font: {
        family: "Font Awesome 5 Free";
        weight: 900;
        size: 3rem;
      }
      content: "\f102";
      color: #3f98ef;
      position: absolute;
      width: 25px;
      height: 25px;
      top: -60px;
      bottom: 0;
      right: 0;
      left: 0;
      margin: auto;
      text-align: center;
    }
    &::after {
      content: "PAGE TOP";
      font-size: 0.9rem;
      color: $white;
      position: absolute;
      top: 55px;
      bottom: 0;
      right: 0;
      left: 0;
      margin: auto;
      text-align: center;
      color: #3f98ef;
    }
  }
}

.footer-nav {
  display: flex;
  justify-content: center;
  list-style: none;
  margin: 0;
  padding-bottom: 2rem;
  border-bottom: 1px solid $white;
  & a {
    padding: 1rem 2.5rem;
    display: block;
    text-decoration: none;
    color: $white;
    & span {
      display: block;
      margin-top: 0.25rem;
      color: $gray;
    }
  }
}
.copy {
  margin: 0;
}

/* =============================================================
===========================タブレット版 ============================
  =============================================================== */

@media screen and (max-width: 960px) {
  * {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  .page-header-clone,
  .scroll-down-btn,
  .footer-nav,
  .grid {
    display: none;
  }
  /* HEADER ====================================*/
  .page-header {
    z-index: 100;
    & .inner {
      display: block;
      & .header-title {
        padding: 1rem;
      }
      & .open-btn {
        display: inline-block !important;
        position: absolute;
        top: 13px;
        right: 10px;
        background-color: $black;
        cursor: pointer;
        width: 50px;
        height: 50px;
        border-radius: 5px;
        & span {
          display: inline-block;
          transition: all 0.5s;
          position: absolute;
          left: 15px;
          height: 5px;
          border-radius: 2px;
          background-color: $white;
          width: 45%;
          &:nth-of-type(1) {
            top: 15px;
          }
          &:nth-of-type(2) {
            top: 23px;
          }
          &:nth-of-type(3) {
            top: 31px;
          }
        }
        &.active span:nth-of-type(1) {
          top: 18px;
          left: 18px;
          transform: translateY(6px) rotate(-45deg);
          width: 30%;
        }
        &.active span:nth-of-type(2) {
          opacity: 0;
        }
        &.active span:nth-of-type(3) {
          top: 30px;
          left: 18px;
          transform: translateY(-6px) rotate(45deg);
          width: 30%;
        }
      }
      & .page-header-content {
        display: block;
        background-color: $black;
        width: 0;
        z-index: 20;
        position: absolute;
        top: 75.59px;
        right: 0;
        overflow: hidden;
        transition: 0.5s;
        &.open-menu {
          width: 100%;
        }
        & .header-nav {
          display: block;
          padding: 0;
          margin: 0;
        }
        & .header-link {
          margin: 1rem 0;
          padding: 0;
          display: flex;
          justify-content: center;
        }
      }
    }
    &.sticky {
      position: fixed;
      top: 0;
      width: 100%;
      opacity: 0.8;
    }
  }
}
