/* 各ページ共通のレイアウト ======================================== */
* {
  padding: 0;
  margin: 0;
}

html {
  font-size: 100%;
  height: 100%;
}

body {
  height: 100%;
  font-family: "Noto Serif JP", sans-serif;
  color: #000;
  background-color: #f5f5f5;
}

p {
  line-height: 1.7;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #000;
}

img {
  object-fit: cover;
}

.page-top-content {
  width: 100%;
  height: 100%;
  background-position: relative;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}
.page-top-content .page-title {
  font-size: clamp(2rem, 1.3rem + 3vw, 4rem);
  color: #fff;
  height: 100%;
  margin: 0 auto;
  text-align: center;
  writing-mode: vertical-rl;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.profile-bg {
  background-image: url("../../dist/images/img2.jpeg");
}

.gallery-bg {
  background-image: url("../../dist/images/img15.jpeg");
}

.news-bg {
  background-image: url("../../dist/images/img21.jpeg");
}

.price-bg {
  background-image: url("../../dist/images/img14.jpeg");
}

.contact-bg {
  background-image: url("../../dist/images/img13.jpeg");
}

.policy-bg {
  background-image: url("../../dist/images/img11.jpeg");
}

.page-load {
  opacity: 0;
}

.page-loaded {
  animation-name: fadeIn;
  animation-duration: 4s;
  animation-delay: 0.5s;
  animation-timing-function: ease;
  animation-fill-mode: forwards;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.wrapper {
  margin: 0 auto;
  padding: clamp(1.25rem, 1rem + 0.98vw, 1.875rem) calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 1.5);
}

.bg {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.img1 {
  background-image: url("../../dist/images/bg1.jpeg");
}

.img2 {
  background-image: url("../../dist/images/bg2.jpeg");
}

.img3 {
  background-image: url("../../dist/images/bg3.jpeg");
}

.img4 {
  background-image: url("../../dist/images/bg4.jpeg");
}

.btn {
  width: auto;
  display: inline-block;
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.5);
  padding: clamp(1rem, 0.95rem + 0.2vw, 1.125rem) calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 2);
  text-decoration: none;
}

/* TOPページのボタン */
.index-btn {
  text-align: center;
  margin: clamp(1.25rem, 1rem + 0.98vw, 1.875rem) auto;
}
.index-btn a {
  position: relative;
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.2);
  color: #696969;
  display: inline-block;
  padding: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.5) clamp(1.25rem, 1rem + 0.98vw, 1.875rem);
  border: 1px solid #696969;
  transition: all 0.4s ease-in-out;
}
.index-btn a::before, .index-btn a::after {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  border: solid #696969;
  transition: all 0.4s ease-in-out;
}
.index-btn a::before {
  top: -6px;
  left: -6px;
  border-width: 1px 0 0 1px;
}
.index-btn a::after {
  bottom: -6px;
  right: -6px;
  border-width: 0 1px 1px 0;
}
.index-btn a:hover {
  color: #fff;
  background-color: #610b0b;
}
.index-btn a:hover:hover::before, .index-btn a:hover:hover::after {
  width: calc(100% + 11px);
  height: calc(100% + 11px);
  border-color: #610b0b;
}

/* スクロール時のフェード機能 【JSで制御】 =============================== */
.scroll-fadeUp {
  display: block;
  opacity: 0;
  transform: translate(0, 80px);
  transition: all 1500ms;
}

.scroll-fadeUp.scrollIn {
  opacity: 1;
  transform: translate(0, 0);
}

.scroll-fadeDown {
  display: block;
  opacity: 0;
  transform: translate(0, -80px);
  transition: all 1500ms;
}

.scroll-fadeDown.scrollIn {
  opacity: 1;
  transform: translate(0, 0);
}

.scroll-fadeLeft {
  display: block;
  opacity: 0;
  transform: translate(-80px, 0);
  transition: all 1500ms;
}

.scroll-fadeLeft.scrollIn {
  opacity: 1;
  transform: translate(0, 0);
}

.scroll-fadeRight {
  display: block;
  opacity: 0;
  transform: translate(80px, 0);
  transition: all 1500ms;
}

.scroll-fadeRight.scrollIn {
  opacity: 1;
  transform: translate(0, 0);
}

/* HEADER ============================================== */
header {
  background-color: #000;
  padding: clamp(10px, 2vw, 15px);
}
header * {
  display: flex;
  align-items: center;
}
header .header-contents {
  justify-content: space-between;
}
header .header-contents .header-title {
  margin: 0 clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
  color: #fff;
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.2);
  font-weight: 900;
}
header .header-contents .header-nav-contents .header-nav li a {
  display: block;
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 0.7);
  padding: clamp(10px, 2vw, 15px);
  color: #fff;
  transition: 400ms;
}
header .header-contents .header-nav-contents .header-nav li a span {
  display: block;
  text-align: center;
  margin-top: 0.25rem;
  color: #696969;
}
header .header-contents .header-nav-contents .header-nav li a:hover {
  background-color: #610b0b;
  color: #fff;
  transition: 400ms;
}
header .header-contents .header-nav-contents .header-link {
  margin: 0 clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
}
header .header-contents .header-nav-contents .header-link li a {
  display: block;
  border-radius: 10%;
  width: 3rem;
  height: 3rem;
  margin: 0.5rem;
  color: #fff;
  position: relative;
  transition: 400ms;
}
header .header-contents .header-nav-contents .header-link li a .fab {
  position: absolute;
  top: 0.5rem;
  left: 0.6rem;
  font-size: 2rem;
}
header .header-contents .header-nav-contents .header-link li a:hover {
  background-color: #610b0b;
  color: #fff;
  transition: 400ms;
}

.header-clone {
  background-color: #000;
  padding: 0.5rem;
  position: fixed;
  top: -130px;
  width: 100%;
  z-index: 30;
  transition: top 0.25s;
  opacity: 0.9;
}
.header-clone * {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-clone > .header-contents .header-title {
  margin: 0 clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
  color: #fff;
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.2);
  font-weight: 900;
}
.header-clone > .header-contents .header-nav-contents .header-nav li a {
  display: block;
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 0.7);
  padding: clamp(10px, 2vw, 15px);
  color: #fff;
  transition: 400ms;
}
.header-clone > .header-contents .header-nav-contents .header-nav li a span {
  display: none;
}
.header-clone > .header-contents .header-nav-contents .header-nav li a:hover {
  background-color: #610b0b;
  color: #fff;
  transition: 400ms;
}
.header-clone > .header-contents .header-nav-contents .header-link {
  margin: 0 clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
}
.header-clone > .header-contents .header-nav-contents .header-link li a {
  border-radius: 10%;
  width: 3rem;
  height: 3rem;
  margin: 0.5rem;
  transition: 400ms;
  position: relative;
}
.header-clone > .header-contents .header-nav-contents .header-link li a .fab {
  color: #fff;
  position: absolute;
  top: 0.6rem;
  left: 0.8rem;
  font-size: 1.5rem;
}
.header-clone > .header-contents .header-nav-contents .header-link li a:hover {
  background-color: #610b0b;
  color: #fff;
  transition: 400ms;
}
.header-clone.visible {
  top: 0;
}

/* TOPページ 【index】======================================= */
.index-top-slider li {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: none;
  opacity: 0;
  animation: anime 50s infinite;
}
.index-top-slider li:nth-child(1) {
  background-image: url("../../dist/images/img12.jpeg");
  animation-delay: 0s;
}
.index-top-slider li:nth-child(2) {
  background-image: url("../../dist/images/img13.jpeg");
  animation-delay: 10s;
}
.index-top-slider li:nth-child(3) {
  background-image: url("../../dist/images/img14.jpeg");
  animation-delay: 20s;
}
.index-top-slider li:nth-child(4) {
  background-image: url("../../dist/images/img15.jpeg");
  animation-delay: 30s;
}

@keyframes anime {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  40% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
.load-down {
  opacity: 0;
  transform: translate(0, -50px);
  transition-timing-function: ease-in-out;
}

.load-downed {
  transform: translate(0, 0);
  opacity: 1;
  transition: all 800ms;
  transition-delay: 200ms;
  transition-timing-function: ease-in-out;
}

.load-up {
  opacity: 0;
  transform: translate(0, 50px);
  transition-timing-function: ease-in-out;
}

.load-uped {
  transform: translate(0, 0px);
  transform: translate(0 0);
  opacity: 1;
  transition: all 800ms;
  transition-delay: 200ms;
  transition-timing-function: ease-in-out;
}

.index-top-text {
  background: rgba(255, 255, 255, 0);
  display: inline-block;
  width: 30vw;
  height: 70vh;
  text-align: center;
  padding: clamp(1rem, 0.95rem + 0.2vw, 1.125rem) calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 2) calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 2);
  position: absolute;
  top: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 2);
  right: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 2);
}
.index-top-text h1 {
  font-size: calc(clamp(2rem, 1.3rem + 3vw, 4rem) * 0.8);
}
.index-top-text .sub-title {
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.1);
  font-weight: 900;
  margin-bottom: clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
}

.index-top-text-bg {
  animation-name: bgChange;
  animation-duration: 1s;
  animation-timing-function: ease;
  animation-fill-mode: forwards;
}

@keyframes bgChange {
  0% {
    background-color: rgba(255, 255, 255, 0);
  }
  100% {
    background-color: rgba(255, 255, 255, 0.9);
  }
}
.index-top-btn {
  display: inline-block;
  text-align: center;
  margin-top: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 1.5 * 0.8);
  padding: clamp(1.25rem, 1rem + 0.98vw, 1.875rem) calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 4);
  border: 2px solid #696969;
  border-radius: 10px;
  z-index: 2;
  animation: bounce 2s infinite 2s;
  transition: all 0.2s ease-in;
  transform: scale(1);
}
.index-top-btn::before {
  position: absolute;
  top: calc(50% - 18px);
  left: calc(50% - 14px);
  transform: rotate(-45deg);
  display: block;
  width: 20px;
  height: 20px;
  content: "";
  border: 2px solid #696969;
  border-width: 0 0 2px 2px;
}

@keyframes bounce {
  0%, 100%, 20%, 50%, 80% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}
.section {
  position: relative;
  padding: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 3) 0 calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 1.5);
}

.section-bg {
  margin: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 2 * 1.5) 0;
}
.section-bg img {
  width: 100%;
  height: 450px;
  object-fit: cover;
}

.section-title {
  text-align: center;
  display: inline-block;
  padding: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 2.5) calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 2);
  color: #fff;
}
.section-title h2 {
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.5);
  margin: clamp(1rem, 0.95rem + 0.2vw, 1.125rem) 0 0.7rem;
}
.section-title span {
  font-size: clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
}

.index-profile-title {
  position: absolute;
  top: -14rem;
  left: 6rem;
  background-color: #0b3b0b;
}

.index-gallery-title {
  position: absolute;
  top: -12rem;
  right: 8rem;
  background-color: #610b0b;
}

.index-job-title {
  position: absolute;
  top: -12rem;
  left: 8rem;
  background-color: #0b0b61;
}

/* TOP NEWS  =========================================== */
.index-news-content {
  margin: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 2);
}
.index-news-content h2 {
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.5 * 1.5);
  margin-bottom: clamp(1.25rem, 1rem + 0.98vw, 1.875rem);
}
.index-news-content .index-news-articles {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: clamp(1.25rem, 1rem + 0.98vw, 1.875rem);
  margin-bottom: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 3);
}
.index-news-content .index-news-articles .index-news-item {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
  border: 1px solid #bbb;
  box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.4);
  transition: background-color 0.5s;
}
.index-news-content .index-news-articles .index-news-item img {
  width: 50%;
  transition: transform 0.5s;
}
.index-news-content .index-news-articles .index-news-item .index-news-text {
  margin-left: clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
}
.index-news-content .index-news-articles .index-news-item .index-news-text h3 {
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.1);
  margin-bottom: clamp(10px, 2vw, 15px);
}
.index-news-content .index-news-articles .index-news-item .index-news-text p {
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 0.7 * 1.2);
  margin-bottom: calc(clamp(10px, 2vw, 15px) * 0.5);
}
.index-news-content .index-news-articles .index-news-item:hover {
  background-color: #dcdcdc;
}
.index-news-content .index-news-articles .index-news-item:hover img {
  transform: scale(1.1);
}

/* TOP Profile, Profile ================================ */
.profile-content {
  margin-bottom: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 3);
  padding: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 2) clamp(1.25rem, 1rem + 0.98vw, 1.875rem);
  background: linear-gradient(to right, #fffcdc, #d9a7c7);
}
.profile-content > * {
  display: flex;
  justify-content: space-around;
}
.profile-content img {
  object-fit: cover;
  width: 35vw;
}
.profile-content p {
  margin-bottom: clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
  font-size: clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
}

.profile {
  flex-direction: row-reverse;
  margin-top: clamp(1.25rem, 1rem + 0.98vw, 1.875rem);
}
.profile .profile-text {
  padding: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 3) calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 1.5) calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 1.5);
  width: 35vw;
  background-color: rgba(255, 255, 255, 0.4);
}
.profile .profile-text span {
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.2);
}
.profile .profile-text h2 {
  font-size: calc(clamp(2rem, 1.3rem + 3vw, 4rem) * 0.6);
  margin: calc(clamp(10px, 2vw, 15px) * 0.5) 0 calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 1.5);
}

.message {
  margin-top: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 3);
}
.message .message-text {
  background-color: rgba(255, 255, 255, 0.4);
  padding: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 1.5);
  width: 35vw;
}
.message .message-text .message-title {
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.5);
  margin-bottom: clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
}

.achievements {
  position: relative;
  margin: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 3) auto;
}
.achievements .achievements-title {
  background-color: #0b0b61;
  position: absolute;
  top: -18rem;
}
.achievements .achievements-content {
  margin-left: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 2);
  padding: clamp(1.25rem, 1rem + 0.98vw, 1.875rem) 0 calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 1.5);
}
.achievements .achievements-content h3 {
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.2);
  margin-bottom: clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
}
.achievements .achievements-content li {
  line-height: 2;
}

/* TOP GALLERY ========================================= */
.index-gallery-content {
  display: grid;
  width: 80vw;
  gap: 2.5vw;
  grid-template-columns: repeat(3, 26vw);
  grid-template-rows: repeat(3, 26vw);
  margin: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 4) auto;
}
.index-gallery-content .index-gallery-item {
  margin: auto;
  position: relative;
  width: 100%;
  height: 100%;
  transition: 0.4s;
  transition-timing-function: ease-in-out;
}
.index-gallery-content .index-gallery-item img {
  display: block;
  width: 100%;
  height: 100%;
}
.index-gallery-content .index-gallery-item .index-gallery-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: opacity 0.3s;
  opacity: 0;
}
.index-gallery-content .index-gallery-item .index-gallery-text * {
  display: block;
  text-align: center;
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.1);
  margin: clamp(10px, 2vw, 15px);
  opacity: 0;
}
.index-gallery-content .index-gallery-item:hover {
  cursor: pointer;
  transform: scale(1.1, 1.1);
  z-index: 10;
}
.index-gallery-content .index-gallery-item:hover .index-gallery-text {
  background-color: #fff;
  border-radius: 50%;
  transition: 0.7s;
  opacity: 0.8;
}
.index-gallery-content .index-gallery-item:hover .index-gallery-text .index-gallery-text1 {
  animation: style-top 0.5s;
  animation-delay: 0.5s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
}
.index-gallery-content .index-gallery-item:hover .index-gallery-text .index-gallery-text2 {
  animation: style-top 0.5s;
  animation-delay: 0.5s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
}
.index-gallery-content .index-gallery-item:hover .index-gallery-text .index-gallery-text3 {
  animation: style-bottom 0.5s;
  animation-delay: 0.5s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
}

@keyframes style-top {
  0% {
    transform: translateY(-50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}
@keyframes style-bottom {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}
/* TOP PRICE =========================================== */
.index-job-content {
  width: 70%;
  display: flex;
  margin: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 3) auto;
}
.index-job-content .index-job-item {
  display: flex;
  flex-flow: column;
  overflow: hidden;
  line-height: 0;
}
.index-job-content .index-job-item img {
  width: 100%;
  height: 305px;
}
.index-job-content .index-job-item .index-job-text {
  background-color: #000;
  color: #fff;
  padding: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 1.5) clamp(10px, 2vw, 15px);
  text-align: left;
  height: 220px;
}
.index-job-content .index-job-item .index-job-text .text1 {
  text-align: center;
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.1);
}
.index-job-content .index-job-item .index-job-text .text2 {
  margin: clamp(1rem, 0.95rem + 0.2vw, 1.125rem) clamp(10px, 2vw, 15px) 0;
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 0.9);
  line-height: 1.8;
}
.index-job-content .index-job-item .number1 {
  order: 1;
}
.index-job-content .index-job-item .number2 {
  order: 2;
}
.index-job-content .index-job-item .number3 {
  order: 4;
}
.index-job-content .index-job-item .number4 {
  order: 3;
}
.index-job-content .index-job-item .number5 {
  order: 5;
}
.index-job-content .index-job-item .number6 {
  order: 6;
}

.index-price-content {
  display: flex;
  justify-content: space-between;
  width: 90%;
  margin: 0 auto;
  padding: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 2) 0;
}
.index-price-content .index-price-item {
  text-align: center;
  width: 30%;
  background-color: #fff;
  border: 1px solid #bbb;
  padding: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 1.5) clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
  margin: 0 clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
}
.index-price-content .index-price-item img {
  height: 30vh;
  width: 23vw;
}
.index-price-content .index-price-item h3 {
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.2);
  margin: clamp(10px, 2vw, 15px) auto calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.2);
}
.index-price-content .index-price-item p {
  font-size: clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
}

/* GALLERY ================================= */
.gallery-content {
  margin: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 3) auto;
}
.gallery-content p {
  text-align: center;
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.1);
  margin-bottom: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 1.5);
  line-height: 2;
}
.gallery-content .gallery {
  width: 80vw;
  display: grid;
  gap: 2vw;
  grid-template-columns: repeat(3, 25vw);
  grid-template-rows: repeat(6, 25vw);
  margin: 0 auto;
}
.gallery-content .gallery-item {
  transition: 0.4s;
  transition-timing-function: ease-in-out;
}
.gallery-content .gallery-item img {
  width: 100%;
  height: auto;
}
.gallery-content .gallery-item .caption {
  display: none;
}
.gallery-content .gallery-item .is-loading {
  opacity: 0;
}
.gallery-content .gallery-item:hover {
  transform: scale(1.1, 1.1);
  z-index: 10;
}

#cboxTitle {
  font-size: clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
  padding: clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
  background-color: #610b0b;
}

/* NEWS ==================================== */
.news-item {
  background-color: #fff;
  width: 80vw;
  display: flex;
  justify-content: space-between;
  padding: clamp(1.25rem, 1rem + 0.98vw, 1.875rem);
  margin: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 2) auto;
  border: 1px solid #bbb;
  box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.4);
  transition: background-color 0.5s;
}
.news-item img {
  transition: transform 0.5s;
}
.news-item .news-text {
  width: 100%;
  margin-left: clamp(1.25rem, 1rem + 0.98vw, 1.875rem);
}
.news-item .news-text h2 {
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.2);
  margin-bottom: clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
}
.news-item .news-text .news-time {
  font-size: clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
  color: #696969;
  margin-bottom: clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
}
.news-item .news-text .news-desc {
  font-size: clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
}
.news-item:hover {
  background-color: #dcdcdc;
}
.news-item:hover img {
  transform: scale(1.1);
}

/* PRICE =================================== */
.price-content {
  max-width: 1000px;
  padding: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 3);
  background-color: #fff;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.2);
  margin: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 4) auto;
}
.price-content .price-title {
  margin-bottom: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 2);
}
.price-content .price-title h3 {
  text-align: center;
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.2);
  margin-bottom: clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
}
.price-content .price-design-title {
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.1);
  font-weight: bold;
  margin-bottom: clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
}
.price-content .price-design {
  border: 1px solid #bbb;
  background-color: #f5f5f5;
  box-sizing: border-box;
  display: flex;
  padding: clamp(1.25rem, 1rem + 0.98vw, 1.875rem);
  margin-bottom: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 2);
}
.price-content .price-design img {
  width: 35vw;
  height: auto;
}
.price-content .price-design .price-design-box {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-left: calc(clamp(1.25rem, 1rem + 0.98vw, 1.875rem) * 2);
}
.price-content .price-design .price-design-box ul {
  width: 70%;
}
.price-content .price-design .price-design-box ul li {
  font-size: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 1.1);
  background-color: #696969;
  color: #fff;
  margin: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 0.5) 0;
  padding: calc(clamp(1rem, 0.95rem + 0.2vw, 1.125rem) * 0.8) clamp(1rem, 0.95rem + 0.2vw, 1.125rem);
  border-radius: 30px;
}
.price-content .price-design .price-design-box .price-design-btn {
  display: inline-block;
  font-size: 1.2rem;
  padding: 1rem 2rem;
  width: auto;
  color: #fff;
  background-color: #000;
  margin: 2rem 0 0 2rem;
  text-align: center;
  text-decoration: none;
}

.price-hosoku {
  margin: 0 auto;
  padding-left: -1rem;
  text-indent: -1rem;
}

.price-table, .price-table2 {
  width: 100%;
  margin-bottom: 1rem;
  border-collapse: collapse;
  border: 1px solid #bbb;
}
.price-table th, .price-table2 th {
  background-color: #f5f5f5;
}
.price-table th, .price-table2 th,
.price-table td,
.price-table2 td {
  width: 35vw;
  padding: 1rem 2rem;
  text-align: center;
  border: 1px solid #bbb;
}
.price-table td:first-child, .price-table2 td:first-child {
  text-align: left;
  background-color: #f5f5f5;
}

.price-table2 td:first-child {
  width: 50%;
}

/* Timeline */
.timeline-item {
  display: flex;
}

.timeline-title {
  font-size: 1.5rem;
  width: 24%;
  padding: 3.5rem 4rem 2rem 0;
  text-align: right;
}

.timeline-content {
  margin: 0;
  width: 76%;
  position: relative;
  border-left: 5px solid #bbb;
}

.timeline1 {
  padding: 2.5rem 0 2.5rem 4rem;
}

.timeline2 {
  padding: 1.5rem 0 1.5rem 4rem;
}

.timeline3 {
  padding: 2rem 0 3.5rem 4rem;
}

.timeline4 {
  padding: 2.5rem 0 1.5rem 4rem;
}

.timeline5 {
  padding: 3.5rem 0 3.5rem 4rem;
}

.timeline-content::before {
  display: block;
  position: absolute;
  top: 2.5rem;
  left: -2.5rem;
  width: 3.5rem;
  height: 3.5rem;
  content: "";
  background-color: #f5f5f5;
  text-align: center;
  /* Font Awesome */
  font-family: "Font Awesome 5 Free";
  font-size: 1.7rem;
  font-weight: 900;
  line-height: 1.5;
  color: #000;
  padding: 0.8rem 0.5rem 0 0.5rem;
  border-radius: 10px;
}

.icon-file::before {
  content: "\f044";
}

.icon-meeting::before {
  content: "\f086";
}

.icon-payment::before {
  content: "\f157";
}

.icon-production::before {
  content: "\f1fc";
}

.icon-complete::before {
  content: "\f4df";
}

.price-btn {
  background-color: #b22222;
  color: #fff;
  margin-top: 3rem;
}

/* CONTACT ================================= */
.contact-text p {
  margin: 0.5rem 0;
}

.small-text {
  font-size: 0.8rem;
  margin-bottom: 2rem;
}

/* Table */
.contact-table {
  border-spacing: 0;
  width: 100%;
  border: 1px solid #bbb;
  margin-bottom: 2rem;
}
.contact-table th,
.contact-table td {
  display: table-cell;
  padding: 1.3rem;
}
.contact-table th {
  font-weight: normal;
  width: 24%;
  vertical-align: middle;
}
.contact-table td {
  width: 76%;
}
.contact-table tr:nth-child(odd) {
  background: #dcdcdc;
}
.contact-table tr:nth-child(even) {
  background: #fff;
}

.must {
  color: #ff0000;
}

/* FORM */
input[type=text],
input[type=email],
input[type=tel],
textarea {
  width: 100%;
  border: 1px solid #bbb;
  background-color: #fff;
  padding: 0.5rem 0 0.5rem 0.5rem;
}

input[type=text]::placeholder,
input[type=email]::placeholder,
input[type=tel]::placeholder,
textarea::placeholder {
  color: #bbb;
}

textarea {
  height: 10rem;
}

#postcode1,
#postcode2 {
  width: 30%;
}

.select-box {
  width: 50%;
  text-align: center;
  padding: 0.5rem;
}

.btn-send {
  color: #fff;
  background-color: #b22222;
}

/* チェックボックスの非表示 */
input[type=checkbox] {
  display: none;
  /* チェックボックスを変更 */
}
input[type=checkbox] + span::before {
  display: inline-block;
  content: "";
  width: 2rem;
  height: 2rem;
  border: 1px solid #bbb;
  background-color: #fff;
  vertical-align: middle;
  margin: -0.25rem 0.5rem 0 1.85rem;
  padding: 0 0 0.25rem 0.5rem;
  /* Font Awesome */
  font-family: "Font Awesome 5 Free";
  font-size: 1.5rem;
  font-weight: 900;
  line-height: 1.5;
}

input[type=checkbox]:checked + span::before {
  content: "\f00c";
}

/* POLICY =========================================== */
.policy-text p {
  margin: 0.5rem 0 3rem;
}
.policy-text h3 {
  font-size: 1.25rem;
  font-weight: 900;
  margin: 0;
}

/* FOOTER =========================================== */
footer {
  text-align: center;
  background-color: #000;
  color: #fff;
}

.footer-policy {
  margin: 0 auto;
  background-color: #000;
}
.footer-policy a {
  text-align: center;
  display: block;
  padding: 2rem;
  color: #fff;
  text-decoration: none;
}

/* .footer-link */
.footer-link {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  padding: 2rem;
}
.footer-link li {
  list-style-type: none;
}
.footer-link li a {
  display: block;
  border-radius: 10%;
  width: 3rem;
  height: 3rem;
  margin: 0;
  padding: 1rem;
  color: #fff;
  position: relative;
}
.footer-link li a .fab {
  position: absolute;
  top: 1.5rem;
  left: 1.5rem;
  font-size: 2rem;
}

/* ページトップに戻るボタン */
.back-to-top {
  position: fixed;
  right: 10px;
  bottom: 10px;
}
.back-to-top a {
  position: relative;
  display: block;
  width: 80px;
  height: 80px;
  text-decoration: none;
}
.back-to-top a::before {
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  font-size: 3rem;
  content: "\f102";
  color: #3f98ef;
  position: absolute;
  width: 25px;
  height: 25px;
  top: -60px;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  text-align: center;
}
.back-to-top a::after {
  content: "PAGE TOP";
  font-size: 0.9rem;
  color: #fff;
  position: absolute;
  top: 55px;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  text-align: center;
  color: #3f98ef;
}

.footer-nav {
  display: flex;
  justify-content: center;
  list-style: none;
  margin: 0;
  padding-bottom: 2rem;
  border-bottom: 1px solid #fff;
}
.footer-nav a {
  padding: 1rem 2.5rem;
  display: block;
  text-decoration: none;
  color: #fff;
}
.footer-nav a span {
  display: block;
  margin-top: 0.25rem;
  color: #696969;
}

.copy {
  margin: 0;
}

/* =============================================================
===========================タブレット版 ============================
  =============================================================== */
@media screen and (max-width: 960px) {
  * {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  .page-header-clone,
.scroll-down-btn,
.footer-nav,
.grid {
    display: none;
  }
  /* HEADER ====================================*/
  .page-header {
    z-index: 100;
  }
  .page-header .inner {
    display: block;
  }
  .page-header .inner .header-title {
    padding: 1rem;
  }
  .page-header .inner .open-btn {
    display: inline-block !important;
    position: absolute;
    top: 13px;
    right: 10px;
    background-color: #000;
    cursor: pointer;
    width: 50px;
    height: 50px;
    border-radius: 5px;
  }
  .page-header .inner .open-btn span {
    display: inline-block;
    transition: all 0.5s;
    position: absolute;
    left: 15px;
    height: 5px;
    border-radius: 2px;
    background-color: #fff;
    width: 45%;
  }
  .page-header .inner .open-btn span:nth-of-type(1) {
    top: 15px;
  }
  .page-header .inner .open-btn span:nth-of-type(2) {
    top: 23px;
  }
  .page-header .inner .open-btn span:nth-of-type(3) {
    top: 31px;
  }
  .page-header .inner .open-btn.active span:nth-of-type(1) {
    top: 18px;
    left: 18px;
    transform: translateY(6px) rotate(-45deg);
    width: 30%;
  }
  .page-header .inner .open-btn.active span:nth-of-type(2) {
    opacity: 0;
  }
  .page-header .inner .open-btn.active span:nth-of-type(3) {
    top: 30px;
    left: 18px;
    transform: translateY(-6px) rotate(45deg);
    width: 30%;
  }
  .page-header .inner .page-header-content {
    display: block;
    background-color: #000;
    width: 0;
    z-index: 20;
    position: absolute;
    top: 75.59px;
    right: 0;
    overflow: hidden;
    transition: 0.5s;
  }
  .page-header .inner .page-header-content.open-menu {
    width: 100%;
  }
  .page-header .inner .page-header-content .header-nav {
    display: block;
    padding: 0;
    margin: 0;
  }
  .page-header .inner .page-header-content .header-link {
    margin: 1rem 0;
    padding: 0;
    display: flex;
    justify-content: center;
  }
  .page-header.sticky {
    position: fixed;
    top: 0;
    width: 100%;
    opacity: 0.8;
  }
}